/* ====================================
   LAYOUT
   ==================================== */

.l-grid {
	@include clearfix();
    padding: 0 20px;
	margin: auto;
    @include breakpoint(1280px) {
        width: 1170px;
        padding: 0;
    }
}
.cols-2 {
	font-size: 0;
	margin-left: -10px;
    margin-right: -10px;
    >* {
    	width: 50%;
	    display: inline-block;
	    vertical-align: top;
	    padding: 0 10px;
    }
}
.cols-3 {
	font-size: 0;
	margin-left: -11px;
    margin-right: -11px;
    >* {
    	width: 33.33%;
	    display: inline-block;
	    vertical-align: top;
	    padding: 0 11px;
    }
}
.cols-4 {
	font-size: 0;
	margin-left: -10px;
    margin-right: -10px;
    >* {
    	width: 25%;
	    display: inline-block;
	    vertical-align: top;
	    padding: 0 10px;
    }
}
.cols-5 {
	font-size: 0;
	margin-left: -10px;
    margin-right: -10px;
    >* {
    	width: 20%;
	    display: inline-block;
	    vertical-align: top;
	    padding: 0 10px;
    }
}
.site-content {
	padding: 30px 0;
}
.area-light {
	padding: 50px 0 65px;
	margin-bottom: 95px;
	background: $secondary;
	&:after {
		display: block;
		content: "";
		width: 1px;
	    height: 130px;
	    position: absolute;
    	left: 50%;
    	border-left: 1px dotted #ccc;
	}
	.ic--emblem {
        background-color: $secondary;
    }
    .zone--congressman {
        .cols-4 {
            &:before {
                width: 100%;
                height: 100%;
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                background: linear-gradient(to top, $secondary 15%, rgba(255, 255, 255, 0) 100%);
            }
        }
    }
}

// Modal
.modal-dialog {
	max-width: 600px;
	margin: 5% auto;
}
.modal-header {
	padding: 12px 20px;
}
.modal-body {
	padding: 20px;
}
.modal-title {
    font-size: 30px;
    font-size: 3rem;
    line-height: 37px;
    line-height: 3.7rem;
    text-transform: capitalize;
}
.close {
    font-size: 35px;
    font-weight: 400;
}